<template>
   <div class="preview-dialog" v-show="show">
      <div class="shadow"></div>
      <div class="box">
         <Button type="warning" @click="hideDialog" class="btn-close">关闭弹窗</Button>
         <preview :show="visible"></preview>
      </div>
   </div>
</template>

<script>
export default {
   name: "preview-dialog",
   props: ["visible"],

   data() {
      return {
         show: false
      };
   },
   components: {
      preview: () => import("../preview/preview")
   },

   methods: {
      showDialog() {
         this.show = true;
      },
      hideDialog() {
         this.show = false;
         this.$emit("hidePreview");
         console.log(this.visible);
      }
   }
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>